.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 4rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card>.img-container {
    height: 100%;
    overflow: hidden;
    position: relative;
}

.card>.img-container>img {
    width: 100%;
    position: relative;
}
nav {
    width: 100%;
    font-size: 2rem;
    color: #fff;
    background-color: rgb(56, 56, 56);
    height: 8%;
    position: fixed;
    top: 0;
    width: 100%;
    margin-bottom: 10px;
    z-index: 1;
}

nav li {
    display: inline-block;
    padding: 26px 0;
    width: 20%;
    display: flex;

}

ul {
    display: flex;
    align-items: center;
}

#rw {
    text-align: center;
    margin-left: 15%;
}

#cur-sco,
#top-sco {
    position: relative;
    left: 15%;
    display: flex;

}
.wrapper {
    padding-top: 50px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    margin: 5% 2% 10% 2%;
    position: relative;
}

footer {
    width: 100%;
    background-color: rgb(56, 56, 56);
    height: 5%;
    position: fixed;
    left: 0;
    bottom: 0;
    margin-top: 10%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

